.list {
  width: 200px;
  background-color: #da67b9;
  min-height: 40px;
  margin: 20px auto;
  color: white;
  border-radius: 5px;
}
.list p {
  padding: 10px;
  position: relative;
}
.list p span {
  position: absolute;
  right: 10px;
  bottom: 10px;
  cursor: pointer;
}
.faicons {
  margin: 0 5px;
  font-size: 15px;
}

.list p input {
  background-color: transparent;
  border: none;
  color: #ffffff;
  font-size: 18px;
}
.list p input:focus {
  outline: none;
}
