body {
  background-color: #b78fdf;
}
.App {
  background-color: #5e2d8f;
  min-height: 600px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form {
  padding-top: 20px;
  text-align: center;
}
#to-do-form input {
  background-color: #479888;
  width: 150px;
  padding: 20px 20px;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 20px;
  font-weight: bold;
}
#to-do-form input:focus {
  outline: 0;
}
#to-do-form input::placeholder {
  color: white;
}
#to-do-form button {
  width: 80px;
  padding: 20px 0;
  color: black;
  background-color: #d7af1d;
  border-radius: 5px;
  margin-left: 5px;
  border: none;
  font-size: 20px;
  font-weight: bold;
}
#to-do-form button:focus {
  outline: 0;
}
